.header-branch{
  padding: .625em 0;
  background: $black;
  color: #fff;
  &__inner,
  &__items,
  &__item{
    display: flex;
    align-items: center;
  }
  &__items{
    margin-left: 4.375em;
  }
  &__item{
    @include lastM(0 3.125em 0 0);
    img{
      width: 2.5625em;
      flex-shrink: 0;
      margin-right: 1.0625em;
      transition: all .3s;
    }
    &:hover{
      img{
        opacity: .8;
      }
    }
  }
}

.header{
  z-index: 20;
  position: relative;
  padding-bottom: 4.75em;
  &__inner{
    padding: 1.44em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 12.0625em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: $border;
  border-bottom: $border;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__links{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
    max-width: 90em;
  }
}

.menu-item{
  &._show{
    .menu-item__show{
      color: $primary;
    }
    .menu-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__link,
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    height: 4.75em;
    span{
      font-size: 1.25em;
      line-height: 140%;
    }
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__show{
    position: relative;
    svg{
      width: 1.75em;
      height: auto;
      flex-shrink: 0;
      margin-left: .19em;
    }
  }
  &__hidden{
    width: 100%;
    z-index: 10;
    @media screen and (min-width: 993px){
      opacity: 0;
      pointer-events: none;
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: -1px;
      padding-top: 3px;
    }
  }
}

.menu-dropdown{
  padding: 1.875em 0 5em;
  border-bottom: $border;
  background-color: #fff;
  &__inner{
    max-width: 77.625em;
    column-count: 4;
    column-gap: 3.125em;
  }
  &__link{
    display: inline-flex;
    width: 100%;
    margin-bottom: 1.25em;
    font-weight: 700;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 1.875em;
    flex-shrink: 0;
    @include lastM(0 .5em 0 0);
    &:hover{
      opacity: .8;
    }
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-contacts,
.header-time{
  &__status{
    display: flex;
    align-items: center;
    i{
      margin-right: .375em;
      flex-shrink: 0;
      width: 0.5em;
      height: 0.5em;
      border-radius: 50%;
    }
  }
}

.header-time{
  margin-left: 1.25em;
  &__status{
    display: flex;
    align-items: center;
    i{
      margin-right: .375em;
      flex-shrink: 0;
      width: 0.5em;
      height: 0.5em;
      border-radius: 50%;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__btn{
    margin-left: 1.25em;
  }
}

.header-search{
  width: 27.25em;
  input{
    border-color: $lightGray;
    background: $lightGray;
    padding-right: 2.5em;
  }
  &__btn{
    position: absolute;
    top: 50%;
    right: .94em;
    transform: translateY(-50%);
    width: 1.5em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.mob-menu{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
  margin-left: 3.25em;
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header-branch{
    display: none;
  }

  .header{
    border-bottom: $border;
    padding-bottom: 0;
    &__inner{
      padding: .5em 0;
      height: 4em;
    }
    &__logo{
      width: 8em;
    }
    &._open-menu{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .mob-menu{
        transform: translateX(0);
      }
    }
  }

  .mob-menu{
    margin: 0;
    position: fixed;
    top: 4em;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh) - 4em + 1px);
    overflow-y: auto;
    background-color: #fff;
    align-items: flex-start;
    flex-direction: column;
    justify-content: unset;
    padding: 5.5em 1em 2em;
    transform: translateX(-150%);
    transition: all .3s;
    border-top: $border;
  }

  .burger{
    margin-left: auto;
    display: flex;
    flex-shrink: 0;
    width: 1.75em;
    height: 1.75em;
    position: relative;
    z-index: 11;
    img{
      transition: all .3s;
      @include centerAbsolute;
      width: 100%;
    }
    ._view2{
      opacity: 0;
    }
  }

  .menu{
    border: none;
    order: -1;
    position: static;
    &__inner{
      display: block;
    }
    &__links{
      flex-direction: column;
      align-items: flex-start;
    }
    .container{
      padding: 0;
    }
    .socials{
      margin: 2em 0 0;
    }
  }

  .menu-item{
    @include lastM(0 0 1em);
    &__show,
    &__link{
      height: unset;
      span{
        font-size: 1.125em;
      }
    }
    &__hidden{
      display: none;
    }
  }

  .header-contacts,
  .header-time{
    margin-top: 1em;
    margin-left: 0;
  }

  .header-contacts{
    order: -1;
    margin-left: 0;
  }

  .header-search{
    order: -1;
    width: calc(100% - 2em);
    margin: 0;
    position: absolute;
    top: 1em;
    left: 1em;
  }

  .header-action{
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    &__btn{
      margin: 1em 0 0;
      width: 100%;
    }
  }

  .menu-dropdown{
    border: none;
    padding: 1em 0;
    &__inner{
      column-count: 1;
      padding-left: 1.5em;
    }
    &__link{
      font-size: .875em;
    }
  }
}
