@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.social-card__item{
  flex-shrink: 0;
  span{
    @include sameNumWidth;
  }
}

._like-btn{
  position: relative;
  cursor: pointer;
  img{
    transition: all .3s;
  }
  ._view2{
    opacity: 0;
    position: absolute;
    z-index: 2;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  &._active{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
}

._fractions{
  font-size: 1em;
  line-height: 140%;
  font-weight: 700;
  .swiper-pagination-current,
  .swiper-pagination-total{
    font-size: inherit;
    line-height: inherit;
  }
}

._bullets{
  position: static;
  margin-top: 1.875em;
  .swiper-pagination-bullet{
    background: $mint;
    opacity: 1;
    width: 0.8125em;
    height: 0.8125em;
    border-radius: .625em;
    margin: 0 .3125em !important;
    &:hover{
      background: $primary;
    }
    &-active{
      background: $primary;
      width: 1.375em;
    }
  }
}

.swiper-button{
  width: 3.75em;
  height: 3.75em;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 1.1875em 1em 0 rgba(49, 55, 76, 0.05), 0 .625em .625em -.375em rgba(49, 55, 76, 0.05);
  svg{
    width: 1.875em;
    height: auto;
  }
  &:hover{
    background-color: $primary;
    path{
      fill: #fff;
    }
  }
}

.slider{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-container{
      width: calc(100% + 2.25em);
    }
    &-slide{
      padding-right: 2.25em;
    }
  }
}

.col4-slider{
  .swiper-slide{
    width: 25%;
  }
}

.col2-slider{
  .swiper-slide{
    width: 50%;
  }
}

.info-block{
  max-width: 55.75em;
  @include grid100gap(.94em);
  .text22{
    opacity: .7;
  }
}

.tabs{
  display: flex;
}

.tab{
  padding: 0 1.25em;
  background-color: rgba(194, 197, 209, 0.30);
  border-radius: 3.125em;
  height: 2.625em;
  display: flex;
  align-items: center;
  font-weight: 700;
  transition: all .3s;
  cursor: pointer;
  @include lastM(0 1.19em 0 0);
  &:hover,
  &._active{
    background-color: $black;
    color: #fff;
  }
  &._active{
    pointer-events: none;
  }
  span{
    font-size: 1em;
    line-height: 140%;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.grid-column6,
.grid-column5,
.grid-column4,
.grid-column3,
.grid-column2{
  display: grid;
  gap: 2.25em;
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1.5em));
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - 1.125em));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 6.75em / 4));
}

.grid-column5{
  grid-template-columns: repeat(5,calc(20% - 9em / 5));
}

.grid-column6{
  grid-template-columns: repeat(6,calc(100% / 6 - 11.25em / 6));
}

.section{
  padding: 4.375em 0;
  overflow: hidden;
  &.section--pb0{
    padding-bottom: 0;
  }
  &--last{
    padding-bottom: 8.75em;
  }
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 3.125em;
  }
}

.rating-input{
  display: flex;
  align-items: center;
  position: relative;
  &__wrap{
    display: flex;
  }
  &__item{
    flex-shrink: 0;
    width: 2.1875em;
    @include lastM(0 .3125em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__field{
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
  }
  &__filled{
    transition: all .3s;
    overflow: hidden;
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    path{
      fill: #FFBD15;
    }
  }
}

.badge{
  display: flex;
  align-items: center;
  padding: 0 .81em;
  height: 2em;
  border-radius: 1.3125em;
  background-color: $lightMint;
  color: $primary;
  font-weight: 700;
  &:hover{
    background-color: $mint;
  }
}

.avatar{
  width: 3.75em;
  height: 3.75em;
  flex-shrink: 0;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 50%;
    object-fit: cover;
  }
}

.video{
  display: block;
  border-radius: 1.125em;
  @include imgCover(68.4%);
  img{
    border-radius: inherit;
  }
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    background-color: rgba(#000,.3);
    z-index: 2;
  }
}

.play-btn{
  @include centerAbsolute;
  width: 6.875em;
  height: 5.625em;
  padding: 0;
  border-radius: .9375em;
  z-index: 3;
  svg{
    width: 3.75em;
    height: auto;
  }
}

.pagination{
  margin-top: 3.125em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__more{
    padding: 0 4.12em;
    height: 4.375em;
    span{
      font-size: 1.25em;
    }
  }
  &__items{
    margin-top: 1.875em;
    border: 1px solid $primary;
    border-radius: .5em;
    display: flex;
    overflow: hidden;
  }
  &__item{
    width: 3.4375em;
    height: 3.4375em;
    font-weight: 700;
    color: $primary;
    @include center;
    &._active,
    &:hover{
      background: rgba(6, 158, 92, 0.20);
    }
    &:not(:last-child){
      border-right: 1px solid $primary;
    }
    &._dots{
      pointer-events: none;
    }
    svg{
      width: 1.875em;
      height: auto;
    }
    span{
      font-size: 1.25em;
      line-height: 147%;
    }
  }
}

.compensate-for-scrollbar{
  padding-right: 0 !important;
}
.fancybox__container{
  width: 100vw !important;
}


@media screen and (max-width: 992px){
  .slider{
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        padding-right: 1em;
      }
    }
  }

  .col4-slider{
    .swiper-slide{
      width: 25em;
    }
  }

  .col2-slider{
    .swiper-slide{
      max-width: unset !important;
      width: 100%;
    }
  }

  ._bullets{
    margin-top: 1.5em;
  }

  .grid-column2{
    grid-template-columns: 100%;
    gap: 1.5em;
  }

  .grid-column3,
  .grid-column4,
  .grid-column5{
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }

  .grid-column6{
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }

  .tabs{
    overflow-x: auto;
    width: calc(100% + 2em);
    margin-left: -1em !important;
    padding: 0 1em;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }

  .section{
    padding: 1em 0;
    &--last{
      padding-bottom: 2em;
    }
    &__top{
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 1em;
    }
    &__link{
      padding-left: 0;
    }
  }

  .rating-input{
    &__item{
      width: 1.25em;
      @include lastM(0);
      &:not(:first-child){
        margin-left: -.25em;
      }
    }
  }

  .video{
    border-radius: 1em;
  }

  .play-btn{
    font-size: .75em;
  }

  .video-card{
    .video{
      margin-bottom: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .slider{
    .swiper-container{
      overflow: unset;
    }
    .swiper-slide{
      max-width: 95%;
    }
  }

  .grid-column3,
  .grid-column4,
  .grid-column5{
    grid-template-columns: 100%;
  }

  .grid-column6{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }

  .play-btn{
    font-size: .5em;
  }

  .pagination{
    margin-top: 2em;
    &__btn{
      height: 3em;
      span{
        font-size: 1em;
      }
    }
    &__items{
      margin-top: 1em;
    }
    &__item{
      width: 2.25em;
      height: 2.25em;
      span{
        font-size: 1em;
      }
      svg{
        width: 1.125em;
      }
    }
  }
}
