.skills,
.experience,
.edu{
  .section__top{
    justify-content: center;
  }
  .info-block{
    text-align: center;
  }
}

.edu{
  &__items{
    width: 80.0625em;
    max-width: 100%;
    margin: 0 auto;
    @include grid100gap(1em);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 0.3125em;
      height: 100%;
      background-color: $primary;
    }
  }
}

.edu-item{
  position: relative;
  @include grid100gap(.875em);
  &:before{
    content: '';
    position: absolute;
    top: 0;
    width: 1.9375em;
    height: 1.9375em;
    background-color: $primary;
    border-radius: 50%;
  }
  &:last-child{
    &:after{
      content: '';
      position: absolute;
      height: calc(100% - 1.9375em);
      width: 1.9375em;
      background-color: #fff;
      bottom: 0;
    }
  }
  @media screen and (min-width: 993px){
    width: calc(50% - 3.72em);
    &:nth-child(odd){
      margin-left: auto;
      &:after,
      &:before{
        right: 100%;
        margin-right: 2.75em;
      }
    }
    &:nth-child(even){
      text-align: right;
      &:after,
      &:before{
        left: 100%;
        margin-left: 2.75em;
      }
    }
  }
}

.experience{
  &__buttons{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  &__btn{
    margin-right: 1.19em;
    @include center;
    padding: 0 1.25em;
    height: 2.625em;
    font-weight: 700;
    transition: all .3s;
    border-radius: 3.125em;
    cursor: pointer;
    color: $black;
    &:hover,
    &._active{
      background-color: $primary;
      color: #fff;
    }
    &._active{
      pointer-events: none;
    }
    span{
      font-size: 1em;
      line-height: 140%;
    }
  }
}

.experience-item{
  display: flex;
  align-items: center;
  &__year{
    width: 12.5em;
    height: 12.5em;
    flex-shrink: 0;
    margin-right: 2.25em;
    background-color: $mint;
    border-radius: 50%;
    @include center;
    text-align: center;
    color: $primary;
  }
}

.experience-slider{
  overflow: hidden;
  width: 54.1875em;
  max-width: 100%;
  margin: 1.94em auto 0;
}

.skills{
  .info-block{
    max-width: 100%;
  }
  .service-nav__links{
    margin-bottom: 3.125em;
  }
  &__desc{
    margin-top: 3.125em;
    .text{
      font-size: 1.125em;
      column-count: 2;
      column-gap: 2em;
    }
  }
}

.doctor-info{
  &__grid{

  }
}

.interests{
  .text{
    font-size: 1.125em;
  }
  &__desc{
    margin-top: 3.125em;
  }
}

.experience2{
  .text48{
    text-align: center;
  }
  .experience__buttons{
    margin-top: 3.125em;
  }
  .experience-slider{
    width: 100%;
    max-width: 40em;
  }
}


@media screen and (max-width: 992px){
  .edu{
    &__items{
      gap: 1.5em;
      &:before{
        left: .8125em;
        transform: unset;
      }
    }
  }

  .edu-item{
    padding-left: 2.5em;
    gap: .25em;
    &:after,
    &:before{
      right: unset;
      left: 0;
    }
  }

  .experience{
    &__buttons{
      justify-content: unset;
    }
  }

  .experience-slider{
    margin-top: 1em;
    margin-left: 0;
  }

  .experience-item{
    &__year{
      font-size: .75em;
    }
  }

  .skills{
    .info-block{
      max-width: 100%;
      text-align: left;
    }
    .service-nav__links{
      margin-bottom: 1.25em;
    }
    &__desc{
      margin-top: 1.25em;
      .text{
        font-size: 1em;
        column-count: 1;
      }
    }
  }

  .doctor-info{
    .grid-column2{
      gap: 2em;
    }
  }

  .interests{
    &__desc{
      margin-top: 1em;
    }
  }

  .experience2{
    .text48{
      text-align: left;
    }
    .experience__buttons{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .experience{
    &__btn{
      padding: 0 .625em;
      height: 2em;
      margin-right: 1em;
      margin-top: .5em;
      span{
        font-size: .875em;
      }
    }
  }

  .experience-item{
    align-items: unset;
    flex-direction: column;
    &__year{
      margin: 0 0 1em;
    }
  }
}
