.contacts{
  &__items{
    margin: 3.125em 0;
  }
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 3.125em;
      flex-shrink: 0;
      margin-right: 2.19em;
    }
  }
}

.gallery-item{
  &._big{
    grid-column: span 2;
    grid-row: span 2;
  }
  &__img{
    min-height: 100%;
    display: block;
    @include imgCover(76.5%);
    img{
      border-radius: 1.25em;
    }
  }
  &__text{
    height: 100%;
    display: flex;
    align-items: center;
  }
}

.error404{
  .text48{
    text-align: center;
  }
  &__grid{
    max-width: 85em;
    margin: 3.125em auto 0;
    display: grid;
    grid-template-columns: 55em calc(100% - 55em);
    align-items: center;
  }
  &__img{
    img{
      width: 126%;
      display: block;
      margin-left: -26%;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .94em;
  }
  &__link{
    text-decoration: underline;
    @include lastM(0 0 .625em);
    span{
      font-size: 1.125em;
      line-height: 150%;
    }
  }
}

.conf{
  .text48{
    text-align: center;
  }
}

.conf-text{
  max-width: 100%;
  width: 75.0625em;
  margin: 3.125em auto 0;
  h3{
    margin-bottom: .5em;
    font-weight: 700;
  }
  p{
    margin-bottom: .78em;
  }
  li,
  p{
    font-size: 1.125em;
    line-height: 150%;
  }
  ol{
    padding-left: 2.25em;
    margin-bottom: .75em;
  }
  hr{
    display: block;
    height: .75em;
    border: none;
  }
}

.faq{
  &__grid{
    margin-top: 3.125em;
  }
}

.qa-card{
  border-radius: 1.25em;
  background-color: $mint;
  @include lastM(0 0 2.25em);
  &._active{
    .qa-card__btn{
      img{
        transform: rotate(-180deg);
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.875em;
    cursor: pointer;
  }
  &__btn{
    width: 1.875em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
      transition: all .4s;
    }
  }
  &__hidden{
    padding: 0 1.875em 1.875em;
    display: none;
  }
}

.analysis{
  grid-column: 1/-1;
  display: flex;
  align-items: flex-start;
  position: relative;
  &__selected{
    margin-left: 2.25em;
  }
  &__input{
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;
    opacity: 0;
  }
}

.analysis-field{
  position: relative;
  &._active{
    .analysis-field__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    height: 3.6875em;
    border-radius: .9375em;
    background-color: #fff;
    border: 1px solid #fff;
    padding: 0 1.5em 0 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all .3s;
    cursor: pointer;
    white-space: nowrap;
    &:focus{
      border-color: $primary;
    }
    svg{
      width: 1.375em;
      height: auto;
      margin-left: 1em;
      flex-shrink: 0;
    }
  }
  &__hidden{
    position: absolute;
    width: 43.75em;
    padding: 1.13em .625em;
    border-radius: 0.9375em;
    border: 1px solid #D9D9D9;
    background: #FFF;
    z-index: 5;
    top: 100%;
    left: 0;
    margin-top: 2.25em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .8125em));
    gap: 1.5em 1.625em;
    padding-right: 2em;
    padding-left: 1.25em;
    overflow-y: auto;
    max-height: 13em;
    @include scroll;
  }
  &__item{
    font-size: 1em;
    line-height: 140%;
    transition: all .3s;
    cursor: pointer;
    &._active,
    &:hover{
      color: $primary;
    }
    &._active{
      font-weight: 700;
    }
  }
}

.analysis-item{
  display: flex;
  align-items: center;
  max-width: 20.9375em;
  &__remove{
    width: 1.875em;
    margin: .13em 0 0 .5em;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    &__items{
      margin: 1em 0;
      display: none;
    }
    &__item{
      img{
        width: 2.125em;
        margin-right: 1em;
      }
    }
  }

  .error404{
    .text48{
      text-align: left;
    }
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
    &__link{
      span{
        font-size: 1em;
      }
    }
  }

  .conf{
    .text48{
      text-align: left;
    }
  }

  .conf-text{
    margin-top: 1.5em;
    li,
    p{
      font-size: .875em;
    }
    ol{
      padding-left: 1em;
    }
  }

  .faq{
    &__grid{
      margin-top: 1.5em;
      gap: 1em;
    }
  }

  .qa-card{
    border-radius: 1em;
    @include lastM(0 0 1em);
    &__show{
      padding: 1em;
    }
    &__hidden{
      padding: 0 1em 1em;
    }
    &__btn{
      width: 1.5em;
    }
  }

  .analysis-field{
    &__show{
      font-size: .875em;
      height: 3.42em;
      border-radius: .6em;
      padding: 0 1.56em;
    }
    &__hidden{
      margin-top: .5em;
      width: 42.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .gallery-item {
    &._big {
      grid-column: unset;
      grid-row: unset;
    }
    &__img{
      img{
        border-radius: 1em;
      }
    }
  }

  .faq{
    .text48{
      font-size: 1.375em;
    }
  }

  .qa-card{
    .text24{
      font-size: 1em;
    }
    &__btn{
      width: 1.25em;
    }
  }

  .analysis{
    @include grid100gap(.5em);
    &__selected{
      margin: 0;
    }
  }

  .analysis-field{
    &__hidden{
      width: 100%;
    }
    &__items{
      padding-right: .5em;
      padding-left: .5em;
      grid-template-columns: 100%;
    }
  }
}
