.prices-filter{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: -1.5em;
  width: calc(100% + .25em);
  .m-btn{
    margin: 1.5em .25em 0 0;
  }
}

.prices-page{
  &__grid{
    margin-top: 3.125em;
  }
  &__content{
    grid-column: 1/3;
  }
  .m-form{
    position: sticky;
    top: 1em;
  }
}

.prices-group{
  padding: 1.875em;
  border-radius: 1.25em;
  border: 2px solid #C2C5D1;
  @include lastM(0 0 2.25em);
  &__items{
    margin-top: 1.5em;
    @include grid100gap(.875em);
  }
  .price-item{
    border: none;
    padding: 0;
    .primary-text{
      color: $black;
      font-weight: 400;
    }
  }
}

.m-form{
  .underline-text{
    white-space: nowrap;
  }
  &__inner{
    padding: 2.5em 3.125em 3.125em;
    border-radius: 1.875em;
    background-color: $mint;
  }
  &__top{
    @include grid100gap(.94em);
    text-align: center;
  }
  &__fields{
    margin-top: 1.875em;
    @include grid100gap(1.25em);
  }
  &__hint{
    margin: .625em 0;
  }
  &__btn{
    width: 100%;
  }
  &__hint2{
    margin-top: 2.75em;
  }
}


@media screen and (max-width: 992px){
  .prices-filter{
    width: calc(100% + 2em);
    margin-left: -1em;
    padding: 0 1em;
    flex-wrap: unset;
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    .m-btn{
      flex-shrink: 0;
      margin-right: 1em;
    }
  }

  .prices-page{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    &__content{
      grid-column: 1/2;
    }
  }

  .prices-group{
    padding: 1em;
    @include lastM(0 0 1.25em);
    &__items{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px) {
  .prices-filter{
    .m-btn{
      min-width: 12em;
    }
  }

  .m-form {
    &__inner {
      padding: 1em;
      border-radius: 1em;
    }
    &__top{
      gap: .5em;
    }
    &__fields{
      gap: 1em;
      margin-top: 1.25em;
    }
    &__hint{
      margin: 0;
    }
    &__hint2{
      margin-top: .75em;
    }
  }
}
