.services-page{
  padding-bottom: 4.375em;
  &__desc{
    margin-top: 1.5em;
  }
  &__grid{
    margin-top: 3.06em;
    &._type2{
      @media screen and (min-width: 993px){
        .service-link{
          padding: 2.37em 6em;
          min-height: 16.75em;
          flex-direction: column;
          justify-content: center;
          text-align: center;
          border-radius: 1.875em;
          img{
            height: 6.25em;
            width: auto;
            margin: 0 0 .875em;
          }
          .text22{
            font-size: 1.75em;
          }
        }
      }
    }
  }
}

.service-link{
  display: flex;
  align-items: center;
  padding: 1.25em;
  min-height: 8.125em;
  border-radius: .9375em;
  background: rgba(215, 241, 232, 0.60);
  color: $primary;
  hyphens: auto;
  &:hover{
    background: $mint;
  }
  img{
    width: 5.625em;
    flex-shrink: 0;
    margin-right: 2.75em;
  }
}

.service-card{
  border-radius: 1.875em;
  border: 1px solid #C2C5D1;
  &__img{
    width: calc(100% + 2px);
    margin: -1px 0 0 -1px;
    @include imgCover(76.8%);
    img{
      border-radius: 1.875em;
    }
  }
  &__info{
    padding: 1.5em .25em;
    text-align: center;
  }
}

.services-collection{
  margin-top: 3.125em;
}

.service-main{
  padding: 3.125em;
  background-color: $primary;
  background-image: url(../img/service-bg.svg);
  background-position: right bottom;
  background-size: 44.1875em auto;
  background-repeat: no-repeat;
  border-radius: 1.125em;
  color: #fff;
  position: relative;
  &__content{
    max-width: 42em;
  }
  &__info{
    @include grid100gap(1.56em);
    .text20{
      max-width: 28em;
    }
  }
  &__price{
    margin-top: 3.69em;
  }
  &__img{
    position: absolute;
    right: 16.75em;
    bottom: 0;
    width: 21.9375em;
    display: block;
  }
}

.service-nav{
  margin-top: 3.0625em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__links{
    display: flex;
    flex-wrap: wrap;
  }
  &__link{
    padding: 0 .625em;
    height: 4.1875em;
    margin-right: 1.125em;
    span{
      font-size: 1.375em;
    }
  }
}

.service-desc{
  margin-top: 1.875em;
  display: grid;
  grid-template-columns: calc(100% - 32.0625em) 30.3125em;
  gap: 1.75em;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.25em;
    }
  }
  .text16{
    margin-top: 2.25em;
  }
}

.prices{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3.84em));
    gap: 1.5em 3.84em;
  }
}

.price-item{
  display: flex;
  justify-content: space-between;
  padding: 1.25em 0;
  border-bottom: 1px solid #D9D9D9;
}

.works{
  .info-block{
    max-width: 100%;
  }
}

.work-card{
  border-radius: 1.875em;
  border: 2px solid #C2C5D1;
  &__top{
    display: grid;
    gap: 2.25em;
    grid-template-columns: repeat(2,calc(50% - 1.125em));
  }
  &__images{
    padding-bottom: 61.2%;
    position: relative;
    overflow: hidden;
    margin: -2px 0 0 -2px;
    width: calc(100% + 4px);
    .beforeAfter{
      position: absolute !important;
      width: 100%;
      height: 100%;
      //overflow: unset !important;
      border-radius: .875em;
      img{
        border-radius: .875em;
      }
      &__separator{
        border: 1px solid #169566;
        background: #FFF !important;
        width: 0.6875em !important;
      }
      &__btn{
        width: 3.9375em !important;
        height: 3.9375em !important;
        @include center;
        i{
          border-color: $primary !important;
        }
      }
    }
  }
  &__info{
    padding: 1.875em .94em 0 0;
  }
  &__doctor{
    display: flex;
    justify-content: space-between;
    .avatar{
      width: 6.5em;
      height: 6.5em;
      margin: 0 0 0 .75em;
    }
    &-info{
      @include grid100gap(.375em);
    }
  }
  &__services{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.5em;
  }
  &__time,
  &__service{
    display: flex;
    align-items: center;
    img{
      width: 1.875em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__service{
    @include lastM(0 0 1em);
  }
  &__time{
    margin-top: 1em;
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 2.25em 1.875em 1.625em;
  }
  .social-card__item{
    flex-shrink: 0;
    margin-left: 2.56em;
    img{
      width: 3.125em;
      margin-right: .625em;
    }
  }
}

.advices{
  &__grid{
    display: grid;
    grid-template-columns: 48.3125em calc(56.3125em);
    gap: 1.25em 8em;
    align-items: center;
  }
}

.advices-text{
  padding: 1.25em;
  position: relative;
  &:before,
  &:after{
    content: '';
    position: absolute;
    width: 3.75rem;
    height: 3.75rem;
  }
  &:before{
    top: 0;
    left: 0;
    border-left: 1px solid $primary;
    border-top: 1px solid $primary;
  }
  &:after{
    bottom: 0;
    right: 0;
    border-right: 1px solid $primary;
    border-bottom: 1px solid $primary;
  }
}

.doctor-card2{
  display: grid;
  grid-template-columns: 15.75em calc(100% - 15.75em);
  align-items: center;
  border-radius: 1.25em;
  border: 1px solid #C2C5D1;
  &._type2{
    border: none;
    .doctor-card2__img{
      min-height: 100%;
      margin: 0;
    }

    .text20{
      text-transform: uppercase;
      letter-spacing: 0.0625em;
    }
    @media screen and (min-width: 993px){
      grid-template-columns: 17em calc(100% - 17em);
      .doctor-card2__content{
        padding: 0 0 0 2.25em;
      }
    }
    .doctor-card__nav{
      max-width: 32.125em;
      justify-content: space-between;
    }
  }
  &__col{
    height: 100%;
  }
  &__img{
    display: block;
    min-height: calc(100% + 2px);
    height: 100%;
    margin: -1px 0 0 -1px;
    @include imgCover(132%);
    img{
      border-radius: 1.25em;
    }
  }
  &__content{
    @include grid100gap(1.875em);
    padding: 1.31em 1.31em 1.31em 3.5em;
  }
  &__info{
    @include grid100gap(1em);
  }
  .doctor-card__nav{
    padding-top: 0;
    margin-top: 0;
    max-width: 23.5em;
  }
}

.service-desc2{
  @media screen and (min-width: 993px){
    grid-template-columns: calc(100% - 48.5em) 46.25em;
  }
}

.services2{
  .services-collection{
    margin-top: 0;
  }
}

.specialization{
  &__title{
    display: flex;
    align-items: center;
    img{
      width: 2.75em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__links{
    margin-top: .875em;
    display: grid;
    grid-template-columns: 1fr .9fr;
    gap: .75em 1.875em;
  }
  &__link{
    line-height: 147%;
  }
}

.lab{
  &__tabs{
    display: flex;
    justify-content: space-between;
  }
  &__tab{
    flex-grow: 1;
    border-radius: 1.25em 1.25em 0 0;
    background-color: rgba(215, 241, 232, 0.60);
    border: 2px solid rgba(215, 241, 232, 0.60);
    height: 4.8125em;
    cursor: pointer;
    transition: all .3s;
    font-weight: 700;
    color: $primary;
    @include center;
    @media screen and (min-width: 993px){
      @include lastM(0 2.25em 0 0);
    }
    &:hover{
      background-color: rgba(215, 241, 232, .80);
      border-color: rgba(215, 241, 232, .80);
    }
    &._active{
      background-color: transparent;
      border-color: $primary;
      pointer-events: none;
    }
    span{
      font-size: 1.5em;
      line-height: 150%;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__items{
    margin-top: -2px;
    border-radius: 0 0 1.25em 1.25em;
    border: 2px solid $primary;
    padding: 1.875em 2.25em;
    @include grid100gap(4em);
  }
}

.analysis-card{
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 47.5em) 43em;
    gap: 1.25em 4.5em;
  }
  &__info{
    @include grid100gap(1.5em);
  }
  &__toggle{
    display: flex;
    width: 1.875em;
    margin-left: auto;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__wrap,
  &__items,
  &__item{
    display: flex;
    align-items: center;
  }
  &__item{
    @include lastM(0 2.25em 0 0);
    img{
      width: 3.125em;
      height: auto;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__wrap{
    justify-content: space-between;
    margin-top: 1.5em;
  }
  &__btn{
    width: 12.1875em;
  }
  &__bottom{
    padding: 1.5em 0 0 2.25em;
    display: none;
  }
  &._active{
    .analysis-card__toggle{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}

.preparing{
  &__text{
    column-count: 2;
    column-gap: 2.25em;
    p,li{
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 992px){
  .services-page{
    padding-bottom: 1em;
    &__desc{
      margin-top: 1em;
    }
    &__grid{
      margin-top: 1.5em;
    }
  }

  .service-link{
    min-height: 5em;
    padding: 1em;
    img{
      width: 4em;
      margin-right: 1em;
    }
  }

  .services-collection{
    margin-top: 2em;
  }

  .service-main{
    padding: 1em;
    background-size: 18em auto;
    &__content{
      max-width: 28em;
    }
    &__img{
      width: 12em;
      right: 1em;
    }
    &__info{
      gap: 1em;
    }
    &__price{
      margin-top: 2em;
    }
  }

  .service-nav{
    margin-top: 2em;
    flex-direction: column;
    align-items: flex-start;
    &__link{
      height: 3em;
      span{
        font-size: 1em;
      }
    }
  }

  .service-desc{
    grid-template-columns: calc(100% - 16.5em) 15em;
    gap: 1.5em;
    .text16{
      margin-top: 1em;
    }
  }

  .prices{
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
  }

  .work-card{
    &__top{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1em 1.5em;
    }
    &__info{
      padding-top: 1em;
    }
    &__doctor{
      .avatar{
        width: 4em;
        height: 4em;
      }
    }
    &__services{
      margin-top: 1em;
    }
    &__service,
    &__time{
      img{
        width: 1.5em;
      }
    }
    &__bottom{
      padding: 1em;
    }
  }

  .advices{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .doctor-card2{
    &._type2{
      @media screen and (min-width: 577px){
        .doctor-card__nav{
          padding-top: 1em;
          @include grid100gap(.5em);
        }
        .doctor-card2__content{
          padding-left: 1em;
          padding-right: 0;
        }
      }
    }
  }

  .specialization{
    &__links{
      grid-template-columns: 100%;
    }
  }

  .lab {
    &__tabs {
      display: flex;
      gap: unset;
      overflow-x: auto;
      width: calc(100% + 2em);
      margin-left: -1em !important;
      padding: 0 1em;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    &__tab{
      min-width: 7em;
      padding: 0 1.25em;
      border-radius: 3.125em;
      height: 2.625em;
      flex-shrink: 0;
      margin-right: 1em;
      border-width: 1px;
      span{
        font-size: 1em;
      }
    }
    &__items{
      margin-top: .5em;
      border-width: 1px;
      padding: 1em;
      border-radius: 1em;
    }
    &__items{
      gap: 2.5em;
    }
  }

  .analysis-card{
    &__top{
      grid-template-columns: 100%;
      position: relative;
    }
    &__title{
      padding-right: 1.75em;
    }
    &__toggle{
      position: absolute;
      top: 0;
      right: 0;
      width: 1.5em;
    }
    &__info{
      gap: .5em;
    }
    &__wrap{
      margin: 0;
    }
    &__bottom{
      padding: 1em 0 0 1em;
    }
  }
}



@media screen and (max-width: 576px){
  .services-page{
    &__grid{
      gap: .5em;
    }
  }

  .service-link{
    min-height: 4em;
    padding: .75em;
    border-radius: .75em;
    img{
      width: 3em;
      margin-right: .75em;
    }
  }

  .service-main{
    &__img{
      display: none;
    }
  }

  .service-desc{
    grid-template-columns: 100%;
    &__img{
      order: -1;
    }
  }

  .price-item{
    padding: .75em 0;
    @include grid100gap(.125em);
  }

  .work-card{
    &__top{
      grid-template-columns: 100%;
    }
    &__info{
      padding: 0 1em;
    }
    .avatar{
      width: 2.5em;
      height: 2.5em;
      margin-left: .5em;
    }
    &__bottom{
      display: block;
    }
    .social-card__item{
      margin: .5em 0 0;
      img{
        width: 2em;
      }
    }
  }

  .advices-text{
    padding: .75em;
    &:before,
    &:after{
      width: 2.5em;
      height: 2.5em;
    }
  }

  .doctor-card2{
    grid-template-columns: 100%;
    &__col{
      height: unset;
    }
    &__img{
      width: calc(100% + 2px);
      padding-bottom: 100%;
    }
    &__content{
      padding: 1em;
      gap: .5em;
    }
    &._type2{
      .doctor-card2__content{
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  .analysis-card{
    &__wrap{
      display: block;
    }
    &__items{
      display: block;
    }
    &__item{
      margin: 0 0 1em !important;
      img{
        width: 2em;
        margin-right: .375em;
      }
    }
    &__btn{
      width: 100%;
    }
  }
}
