.fg {
  position: relative;
  margin: 0;
  .nice-select,
  select,
  textarea,
  input {
    border: 1px solid #fff;
    border-radius: 0.9375em;
    background-color: #fff !important;
    padding: 0 1.56em;
    height: 3.6875em;
    width: 100%;
    font-size: 1em;
    color: $black;
    &::placeholder {
      color: $darkGray;
    }
    &:focus{
      border-color: $primary;
    }
  }
  textarea{
    height: 12.5em;
    padding-top: 1.2em;
    padding-bottom: 1.2em;
    resize: none;
  }
  .nice-select{
    display: flex;
    align-items: center;
    .current{
      width: 100%;
      background-image: url(../img/arrow3.svg);
      background-repeat: no-repeat;
      background-position: right center;
      background-size: 1.875em auto;
    }
    .list{
      padding: 1.125em .625em;
      border-radius: 0.9375rem;
      border: 1px solid #D9D9D9;
      background: #FFF;
      box-shadow: unset;
      @include grid100gap(1.5em);
    }
    .option{
      padding: 0 1.25em;
      border: none !important;
      &.selected,
      &:hover{
        background-color: transparent;
        color: $primary;
      }
      &.disabled{
        display: none;
      }
    }
  }
  &._gray{
    .nice-select,
    select,
    textarea,
    input{
      background-color: #F5F5F5 !important;
      border-color: #F5F5F5;
      &:focus{
        border-color: $primary;
      }
    }
  }
}

.file-input{
  position: relative;
  color: $darkGray;
  margin-bottom: 1.25em;
  &__inner{
    background-color: #fff;
    padding: 0 1.56em;
    height: 3.6875em;
    border-radius: 0.9375em;
    display: flex;
    align-items: center;
    img{
      width: 2.1875em;
      margin-left: .69em;
      flex-shrink: 0;
    }
  }
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
  }
  &__hint{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .625em;
    font-style: italic;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-color: $primary;
      }
    }
  }
  label {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    font-size: 1em;
    &:before {
      content: '';
      flex-shrink: 0;
      width: 0.9375em;
      height: 0.9375em;
      transition: all .100s;
      margin-right: .75em;
      border-radius: .3125em;
      background: #F5F5F5;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    .nice-select,
    textarea,
    input{
      font-size: .875em;
      border-radius: .6em;
      height: 3.42em;
    }
    textarea{
      height: 7em;
    }
  }
}
