.about-doctor{
  overflow: hidden;
  .doctor-card__nav{
    display: flex !important;
    justify-content: unset !important;
    gap: unset !important;
  }
  .doctor-card__btn{
    margin-right: 1em;
    &:last-child{
      margin-right: 0;
    }
  }
  .specialization__links{
    row-gap: .3125em;
  }
  .doctor-card2__experience{
    padding: 0 1.25em;
    background-color: rgba($primary,.1);
    height: 2.625em;
    display: inline-flex;
    align-items: center;
    border-radius: 6.25em;
    margin-right: auto;
  }
  @media screen and (min-width: 993px){
    .advices__grid{
      grid-template-columns: 53.75em calc(100% - 53.75em - 3.125em);
      gap: 3.125em;
      align-items: flex-start;
    }
    .text48{
      font-size: 2.625em;
    }
    .page-top{
      margin-bottom: 1.875em;
    }
  }
}

.doctor-form{
  padding: 2em 0 2em 3.125em;
  position: relative;
  margin-top: -5.3125em;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 44000px;
    background-color: rgba($primary,.1);
    border-radius: .9375em 0 0 .9375em;
    z-index: -1;
  }
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(.625em);
  }
  textarea{
    height: 7.75em;
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
}

.doctor-content{
  padding: 0 0 5em;
  margin-top: -1.75em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3.125em));
    gap: 4.5em 6.25em;
  }
  &__group{
    margin-top: 4.5em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__title{
    margin-bottom: 1.25em;
    padding: 1.25em 0;
    border-bottom: 1px solid rgba($gray,.3);
  }
  .experience2{
    .experience__buttons{
      justify-content: unset;
      margin-top: 0;
    }
    .experience-slider{
      margin-left: 0;
    }
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
    justify-content: center;
  }
  @media screen and (min-width: 993px){
    .edu-item{

    }
  }
}

.doctor-skills{
  &:not(._show-all){
    .text{
      ul:nth-child(2)~*{
        display: none;
      }
    }
  }
  &._show-all{
    .doctor-skills__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__more{
    margin-top: 1.875em;
    display: flex;
    align-items: center;
    font-weight: 700;
    color: $primary;
    svg{
      width: 1.5em;
      margin-left: .3125em;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .doctor-form{
    padding: 1em;
    margin: 0 0 1em;
    &:before{
      width: 100%;
    }
  }

  .doctor-content{
    padding-bottom: 3.125em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){

}


.doctor-content{
  .edu__items._desktop{
    display: none;
  }
  @media screen and (min-width: 993px){
    .edu__items._mob{
      display: none;
    }
    .edu__items._desktop{
      margin-top: 5em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 3.72em));
      align-items: flex-start;
      justify-content: space-between;
      .edu-item{
        width: 100%;
      }
      .edu__items-col{
        @include grid100gap(2em);
        &:nth-child(1){
          padding-top: 3em;
          .edu-item{
            text-align: right;
            &:before{
              left: 100%;
              right: unset;
              margin: 0 0 0 2.75em;
            }
          }
        }
        &:nth-child(2){
          .edu-item{
            text-align: left;
            &:before{
              left: unset;
              right: 100%;
              margin: 0 2.75em 0 0;
            }
          }
        }
      }
    }
  }
}
