.main{
  padding: 2.19em 0 4.375em;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 66% calc(34% - 2.25em);
    gap: 2em 2.25em;
  }
}

.main-slider,
.main-slider2{
  overflow: hidden;
  position: relative;
  border-radius: 1.125em;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      left: unset;
      right: 3em;
      bottom: 2.87em;
      width: unset;
      z-index: 3;
    }
  }
}

.main-slider{
  background-color: $primary;
  background-image: url(../img/banner-bg.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: right top;
  .swiper{
    &-pagination{
      color: $primary;
    }
  }
}

.main-slider2{
  overflow: unset;
  .swiper{
    &-container{
      border-radius: 1.125em;
      overflow: hidden;
    }
    &-pagination{
      color: #fff;
      right: 2.56em;
      bottom: 2.37em;
    }
    &-button{
      &-prev{
        left: -1.56em;
      }
      &-next{
        right: -1.56em;
      }
    }
  }
}

.banner,
.service-banner{
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 37.125em;
  position: relative;
}

.banner{
  padding: 3.125em 29em 3.125em 3.125em;
  color: #fff;
  &__info{
    @include grid100gap(1.56em);
  }
  &__text{
    max-width: 28.1em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 3em;
    display: flex;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    img{
      height: 100%;
      display: block;
    }
  }
  &__doctor{
    position: absolute;
    right: 3em;
    bottom: 9.81em;
    z-index: 2;
    color: $black;
    padding: .44em 1.75em .625em 1.81em;
    border-radius: 0.4375em;
    border: 1px solid #D7F1E8;
    background: rgba(215, 241, 232, 0.40);
    backdrop-filter: blur(17.5px);
    &:before{
      content: '';
      position: absolute;
      top: .75em;
      left: .81em;
      width: 0.4375em;
      height: 0.4375em;
      background-color: $lime;
      border-radius: 50%;
      box-shadow: 1px 2px 2px 0px rgba(255, 255, 255, 0.25) inset, 0px -1px 2px 0px rgba(0, 0, 0, 0.15) inset;
    }
  }
}

.service-banner{
  padding: 3.28em 2.5em 3.125em;
  border-radius: 1.125em;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .banner__info,
  .banner__nav{
    position: relative;
    z-index: 2;
  }
}

.advantage-slider{
  margin-top: 1.875em;
}

.our-services,
.trust{
  padding: 4.375em 0;
}

.service-search{
  margin-top: 4.69em;
  padding: 3.38em 4.38em 3.38em 13.25em;
  border-radius: 0.9375em;
  background-color: $lightMint;
  position: relative;
  display: flex;
  align-items: center;
  &__img{
    position: absolute;
    left: .94em;
    bottom: 0;
    width: 11.5625em;
    display: block;
  }
  &__title{
    white-space: nowrap;
  }
  &__form{
    flex-grow: 1;
    margin-left: 3.125em;
    display: grid;
    grid-template-columns: calc(100% - 9.69em) 8.75em;
    gap: .94em;
  }
  &__btn{
    height: 100%;
  }
}

.services{
  margin-top: 3.69em;
  &__top{
    display: flex;
    align-items: center;
  }
  .tabs{
    margin-left: 1.875em;
  }
  &__links{
    margin-left: 5.31em;
    display: flex;
    .m-link{
      margin-right: 3.69em;
    }
  }
  &__bottom{
    margin-top: 3.125em;
  }
  &__links2{
    column-gap: 2.25em;
    column-count: 4;
    position: relative;
  }
  &__link{
    font-size: 1.375em;
    line-height: 140%;
    margin-bottom: .9em;
    display: inline-flex;
    width: 100%;
    &:nth-last-child(2){
      margin-bottom: 4em;
    }
  }
  &__nav{
    position: absolute;
    right: 0;
    bottom: .5em;
    width: calc(25% - 6.75em / 4);
    display: flex;
  }
  &__btn{
    padding: 0 .625em;
  }
}

.trust{
  &__grid{
    margin-top: 3.125em;
  }
}

.location{
  padding-top: 4.375em;
  &__bottom{
    margin-top: 1.875em;
    position: relative;
  }
}

.location-info{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 3.125em;
  border-radius: 0.9375em;
  background-color: #fff;
  width: 26.5em;
  z-index: 3;
  padding: 1.875em 2.5em 2.5em;
  @media screen and (min-width: 1920px){
    left: calc((100% - 1920px) / 2 + 3.125em);
  }
  &__top{
    @include grid100gap(1.25em);
  }
  &__group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__item{
    display: flex;
    align-items: center;
    margin-top: .625em;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
  &__bottom{
    margin-top: 2.5em;
    @include grid100gap(.94em);
  }
}

.map{
  height: 43.75em;
}

[class*="ymaps-2"][class*="-ground-pane"] {
  filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale");
  -webkit-filter: grayscale(100%);
}

.place-card{
  &__top{
    display: flex;
    align-items: center;
    svg{
      width: 1.875em;
      height: auto;
      margin: 0 .3125em;
    }
  }
}

.ymaps-2-1-79-balloon__content{
  font-size: 1em !important;
  padding: .34em .69em .72em !important;
  min-width: 10.6875em !important;
  min-height: 3em !important;
  &>*{
    height: 100% !important;
    width: 100% !important;
  }
}

.ymaps-2-1-79-balloon__tail,
.ymaps-2-1-79-balloon__tail:after{
  display: none !important;
}

.ymaps-2-1-79-balloon{
  padding: 0 !important;
  transform: translate(110px,28px);
  box-shadow: none !important;
}

.ymaps-2-1-79-balloon__layout{
  backdrop-filter: blur(17.5px);
  border-radius: 0.4375em;
}

.introduction-slider{
  .swiper{
    &-slide{
      width: 25%;
      &._big{
        width: 50%;
      }
    }
  }
}

.feedback{
  padding: 3.125em 0 3.75em;
  margin: 4.375em 0;
  background-color: $mint;
  overflow: hidden;
  .swiper-pagination-bullet:not(.swiper-pagination-bullet-active){
    background-color: #fff;
  }
}

.feedback2{
  margin-bottom: 0;
  padding-bottom: 8.75em;
}

.new-feedback{
  height: 100%;
  padding: 2.875em 2.75em 2.13em;
  border-radius: .9375em;
  background-color: $primary;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  &__img{
    margin-top: 2.25em;
    width: 16.4em;
    max-width: 100%;
    display: block;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2.25em;
  }
  &__btn{
    width: 100%;
  }
}

.have-questions{
  padding: 4.375em 0;
  &__inner{
    border-radius: .9375em;
    background-color: $primary;
    padding: 3.06em 9.75em 3.06em 31.56em;
    position: relative;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__img{
    position: absolute;
    bottom: 0;
    left: 5.56em;
    width: 25.3125em;
    display: block;
  }
  &__info{
    max-width: 51em;
    @include grid100gap(.625em);
  }
  &__btn{
    margin-left: 1em;
    flex-shrink: 0;
    width: 17.0625em;
    max-width: 100%;
  }
}

.consult{
  padding: 7em 0 4.375em;
  &--last{
    padding-bottom: 8.75em;
  }
  &__inner{
    background-image: url(../img/consult-bg.svg);
    background-size: auto 100%;
    background-repeat: no-repeat;
    background-color: $mint;
    border-radius: 1.125em;
    padding: 3.44em 2.69em 3.44em 48.31em;
    position: relative;
  }
  &__hint{
    margin-top: 4.56em;
    a{
      white-space: nowrap;
    }
  }
  &__img{
    position: absolute;
    width: 37.6875em;
    display: block;
    left: 7.81em;
    bottom: 0;
  }
}

.consult-form{
  display: grid;
  grid-template-columns: calc(100% - 35.9375em) 17.0625em 14.375em;
  gap: 2.25em;
  margin-top: 3.125em;
  &__btn{
    padding: 0;
  }
}

.videos{
  &__grid{
    margin-top: 3.75em;
  }
}

.video-article{
  @media screen and (min-width: 993px){
    grid-template-columns: calc(100% - 48.3125em) 46.0625em;
    .video{
      padding-bottom: 57%;
    }
  }
  &__content{
    padding: 3.125em;
    @include grid100gap(3.125em);
  }
  &__nav{
    display: flex;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 1em 0;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .main-slider{
    border-radius: 1em;
  }

  .main-slider2{
    .swiper-button{
      display: none;
    }
  }

  .service-banner,
  .banner{
    padding: 1.25em;
    min-height: 25em;
    &__info{
      gap: 1em;
    }
    &__text{
      max-width: 25em;
    }
  }

  .banner{
    padding-right: 2em;
  }

  .service-banner{
    min-height: 18em;
  }

  .trust,
  .our-services{
    padding: 1em 0;
  }

  .service-search{
    margin-top: 1.5em;
    padding: 1em;
    display: block;
    &__img{
      display: none;
    }
    &__title{
      white-space: unset;
    }
    &__form{
      margin: 1em 0;
    }
  }

  .services{
    margin-top: 2em;
    &__top{
      display: block;
    }
    &__links,
    .tabs{
      margin: 1em 0 0;
    }
    &__bottom{
      margin-top: 2em;
    }
    &__links2{
      column-count: 2;
    }
    &__link{
      font-size: .875em;
      &:nth-last-child(2){
        margin-bottom: 2.5em;
      }
    }
    &__nav{
      width: calc(50% - 1.125em);
    }
  }

  .trust{
    &__grid{
      margin-top: 1.5em;
    }
  }

  .location{
    padding-top: 1em;
    &__bottom{
      margin-top: .5em;
    }
  }

  .location-info{
    border-top: $border;
    border-radius: 0;
    position: static;
    width: 100%;
    transform: unset;
    padding: 1em;
    &__top{
      gap: .75em;
    }
    &__bottom{
      margin-top: 1.5em;
      gap: .75em;
    }
  }

  .map{
    height: 25em;
  }

  .feedback{
    margin: 1em 0;
    padding: 1em 0;
  }

  .feedback{
    margin-bottom: 0;
    padding-bottom: 2em;
  }

  .have-questions{
    padding: 1em 0;
    &__inner{
      padding: 1em;
    }
    &__img{
      display: none;
    }
  }

  .consult{
    padding: 1em 0;
    &--last{
      padding-bottom: 2em;
    }
    &__inner{
      padding: 1em;
      background-image: unset;
    }
    &__img{
      display: none;
    }
    &__hint{
      margin-top: 1.5em;
    }
  }

  .consult-form{
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1em 1.5em;
    margin-top: 1.5em;
    &__btn{
      grid-column: 1/-1;
      order: 10;
    }
  }

  .videos{
    &__grid{
      margin-top: 1.5em;
    }
  }

  .video-article{
    &__content{
      padding: 1em 0;
      gap: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    .swiper-pagination{
      color: #fff;
      right: 1em;
      bottom: 1.9em;
    }
  }

  .banner{
    padding: 1em;
    &__img,
    &__doctor{
      display: none;
    }
  }

  .service-search{
    &__form{
      grid-template-columns: 100%;
    }
    &__btn{
      height: 3em;
    }
  }

  .services{
    &__links{
      flex-wrap: wrap;
      .m-link{
        margin: .5em 1.5em 0 0;
      }
    }
    &__links2{
      column-count: 1;
    }
    &__nav{
      position: static;
      width: 100%;
    }
  }

  .introduction-slider{
    .swiper{
      &-slide{
        width: 50%;
        &._big{
          width: 100%;
        }
      }
    }
  }

  .have-questions{
    padding: 1em 0;
    &__inner{
      display: block;
    }
    &__btn{
      margin: 1em 0;
      width: 100%;
    }
  }

  .consult-form{
    grid-template-columns: 100%;
  }

  .video-article{
    &__link{
      width: 100%;
    }
  }
}
