.tax{
  .text48{
    text-align: center;
  }
  &__groups{
    margin: 2.125em auto 0;
    width: 75.0625em;
    max-width: 100%;
    @include grid100gap(3.25em);
  }
}

.tax-group{
  @include grid100gap(2.625em);
  &__title{
    text-align: center;
  }
  &__text{
    font-size: 1.125em;
    hr{
      height: .75em;
      display: block;
      border: none;
      opacity: 0;
    }
  }
}

.tax-form{
  @include grid100gap(2.25em);
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
  .tax-files__label,
  .persons__label,
  .passport__label,
  .fg label{
    font-size: 1em;
    line-height: 140%;
    font-weight: 600;
  }
  .fg{
    display: flex;
    align-items: center;
    label{
      margin-right: 1.5em;
      flex-shrink: 0;
    }
    &._full{
      grid-column: 1/-1;
      display: block;
      label{
        display: block;
        margin: 0 0 .75em;
      }
    }
  }
  &__confirm{
    label{
      align-items: flex-start;
      line-height: 150%;
      &:before{
        transform: translateY(.2em);
      }
    }
  }
  &__nav{
    margin-top: 15em;
    display: flex;
    justify-content: center;
    .m-btn{
      width: 21em;
      max-width: 100%;
      height: 4.375em;
      span{
        font-size: 1.25em;
      }
    }
  }
}

.passport{
  margin-top: .75em;
  &__fields{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: 16.7% 18.6% 18.6% calc(46.1% - 4.5em);
    gap: 1.5em;
    &._type2{
      grid-template-columns: 26.5em calc(100% - 28em);
    }
  }
}

.persons{
  margin-top: .75em;
  &__checks{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.5em;
  }
  .m-check{
    margin: .75em 1.5em 0 0;
  }
  &__content{
    margin-top: 2.25em;
    display: none;
    &._active{
      display: block;
    }
  }
  &__group{
    @include grid100gap(1.5em);
  }
}

.trust-get,
.delivery-way{
  .tax-form__fields{
    grid-template-columns: 100%;
  }
}

.trust-get{
  .persons__checks,
  .m-check{
    margin: 0;
  }
}

.file{
  display: flex;
  align-items: center;
  margin-right: 1.5em;
  span{
    font-size: 1em;
    line-height: 140%;
  }
  svg{
    width: .75em;
    height: auto;
    margin-left: .3em;
  }
}

.tax-files{
  display: flex;
  align-items: center;
  .tax-files__label{
    margin: 0 1.5em 0 0;
  }
  .files{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  .file-input{
    position: relative;
    margin: 0;
    height: 4em;
    padding: 0 1.56em;
    @include center;
    font-weight: 600;
    color: $primary;
    span{
      font-size: 1.125em;
      line-height: 147%;
    }
  }
}


@media screen and (max-width: 992px){
  .tax {
    .text48 {
      text-align: left;
    }
  }

  .tax-group{
    gap: .625em;
    &__title{
      text-align: left;
    }
    &__text{
      font-size: 1em;
      hr{
        height: .5em;
      }
    }
  }

  .tax-form{
    gap: 1.75em;
    .fg{
      display: block;
      label{
        display: block;
        margin: 0 0 .5em;
      }
    }
    &__nav{
      margin-top: 3em;
    }
  }

  .passport{
    &__fields{
      grid-template-columns: 100% !important;
    }
  }

  .tax-files{
    display: block;
    &__label{
      margin: 0 0 .75em;
    }
    .file-input{
      height: 3em;
      padding: 0;
      span{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .tax-form{
    &__fields{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__nav{
      margin-top: 1em;
      .m-btn{
        height: 3em;
        span{
          font-size: 1em;
        }
      }
    }
  }
}
