@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.56em;
    height: 3.6875em;
    border-radius: .9375em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 140%;
    }
    svg,
    img{
        width: 1.5em;
        height: auto;
        flex-shrink: 0;
        &:first-child{
            margin-right: .625em;
        }
        &:last-child{
            margin-left: .625em;
        }
    }
}


.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
    &._light{
        background-color: $lime;
        &:hover{
            background-color: darken($lime,10%);
        }
    }
}


.m-btn-primary-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}

.m-btn-transparent{
    color: $primary;
    &:hover{
        opacity: .8;
    }
}

._fill-hover{
    &:hover{
        path{
            fill: $primary;
        }
    }
}

._stroke-hover{
    &:hover{
        path{
            stroke: $primary;
        }
    }
}

.m-link{
    font-weight: 700;
    text-decoration: underline;
}


@media screen and (max-width: 992px){
    .m-btn{
        padding: 0 1em;
        height: 3em;
        border-radius: .5em;
        span{
            font-size: .875em;
        }
    }
}
