.page-top{
  margin-bottom: 3.125em;
  &._mb0{
    margin-bottom: 0;
  }
}

.about{
  padding-bottom: 4.375em;
  overflow: hidden;
  &--last{
    padding-bottom: 8.75em;
  }
  @media screen and (min-width: 993px){
    .advices__grid{
      grid-template-columns: 53.25em calc(100% - 65.25em);
      justify-content: space-between;
      gap: 2em;
    }
  }
  &__grid{
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.25em;
    }
  }
  .video{
    padding-bottom: 67.3%;
    &:before{
      display: none;
    }
  }
  .text16{
    line-height: 150%;
  }
}

.advantages{
  overflow: hidden;
}

.license-info{
  align-self: flex-start;
  padding-top: .625em;
  @include grid100gap(.625em);
}

.license-slider{
  margin-top: 4.375em;
  .swiper{
    &-slide{
      width: calc(100% / 6);
    }
  }
}

.license-card{
  &__img{
    display: block;
    @include imgCover(145.6%);
    &:before{
      content: '';
      @include centerAbsolute;
      width: 4em;
      height: 4em;
      background-image: url(../img/zoom2.svg);
      @include bgImgSize(3.875em);
      z-index: 2;
    }
  }
}

.doc-info{
  &__group{
    @include lastM(0 0 1.875em);
  }
  &__col{
    &:nth-child(2){
      grid-column: 2/4;
    }
  }
}

.doc-info2{
  row-gap: 1.25em;
  &__group{
    @include grid100gap(.44em);
  }
}

.docs{
  &__grid{
    align-items: center;
  }
}

.doc-item{
  display: flex;
  align-items: center;
  img{
    width: 3.125em;
    flex-shrink: 0;
    display: block;
    margin-right: .625em;
  }
  .text22{
    text-decoration: underline;
  }
}

.text{
  ul,
  p{
    @include lastM(0 0 1.25em);
  }
  h3{
    font-weight: 700;
  }
  img{
    width: 100%;
    display: block;
    border-radius: 1.25em;
    margin: 1.5em 0;
  }
  ol{
    counter-reset: list;
    li{
      counter-increment: list;
      &:before{
        content: counter(list) '. ';
        @include sameNumWidth;
      }
    }
  }
}

.social-project{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2em;
    .social-card__items{
      max-width: 28.3125em;
      flex-grow: 1;
      margin: 0;
      img{
        width: 2.1875em;
      }
    }
  }
  .text{
    p{
      line-height: 150%;
      margin: 0 !important;
    }
  }
}

.share{
  display: flex;
  align-items: center;
  &__item{
    width: 2.1875em;
    flex-shrink: 0;
    margin-left: .75em;
    img{
      transition: inherit;
      width: 100%;
    }
    &:hover{
      img{
        transform: translateY(-40%);
      }
    }
  }
}

.achievement{
  margin-top: 3.125em;
  padding: 0 5.66em 0 8.13em;
  border-radius: 1.125em;
  background-color: $mint;
  background-image: url(../img/achievement-bg.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  &__img{
    width: 23.25em;
    flex-shrink: 0;
    display: block;
    margin-right: 15em;
  }
  &__text{
    font-size: 2.625em;
    line-height: 130%;
    font-weight: 600;
    color: $primary;
    span{
      font-size: 1.14em;
      font-weight: 700;
    }
  }
}

.review-page{
  @media screen and (min-width: 993px){
    &__col{
      &:nth-child(2){
        grid-column: 2/4;
      }
    }
    .vacancy-form{
      padding-bottom: 2.56em;
    }
    .vacancy-form__fields{
      grid-template-columns: calc(100% - 32.3125em) 31.0625em;
      gap: 1.25em;
    }
    ._text-field{
      grid-row: 1/3;
      grid-column: 2/3;
      textarea{
        height: 100%;
      }
    }
    .text{
      p{
        font-size: 1.375em;
        @include lastM(0 0 1.5em);
      }
    }
  }
  .vacancy-form__btn{
    margin: 0;
  }
}

.filter{
  margin-bottom: 3.125em;
}

.insurance{
  padding-bottom: 4.375em;
  &__info{
    margin-top: 3.125em;
    max-width: 53em;
    @include grid100gap(1.5em);
    .text18{
      max-width: 45.6em;
    }
  }
  &__grid{
    margin-top: 2.25em;
  }
}

.insurance-card{
  text-align: center;
  min-height: 9.375em;
  &__img{
    display: block;
    position: relative;
    padding-bottom: 30%;
    margin-bottom: .625em;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}

.live-item2{
  @media screen and (min-width: 993px){
    .m-form{
      &__inner{
        padding-left: 2.75em;
        padding-right: 2.75em;
      }
      .text48{
        font-size: 2em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .page-top{
    margin-bottom: 1em;
  }

  .about{
    padding-bottom: 1em;
    &--last{
      padding-bottom: 2em;
    }
    &__grid{
      margin-top: 1em;
    }
  }

  .license-info{
    padding-top: 0;
  }

  .license-slider{
    margin-top: 2em;
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }

  .doc-info{
    &__col{
      &:nth-child(2){
        grid-column: 2/3;
      }
    }
  }

  .doc-item{
    img{
      width: 2.5em;
    }
    .text22{
      font-size: .875em;
    }
  }

  .social-project{
    &__top{
      .social-card__items{
        max-width: 22em;
        img{
          width: 1.75em;
        }
      }
    }
  }

  .share{
    &__item{
      width: 1.5em;
    }
  }

  .achievement{
    flex-direction: column;
    background-image: unset;
    padding: 0 1em 1em;
    margin: 2em 0 1em;
    &__img{
      margin: 0 0 1em;
      max-width: 100%;
    }
    &__text{
      font-size: 1.75em;
    }
  }

  .filter{
    margin-bottom: 1em;
    row-gap: .75em;
  }

  .insurance{
    &__info{
      gap: .5em;
      margin-top: 1.5em;
    }
  }

  .insurance-card{
    min-height: 6em;
  }
}


@media screen and (max-width: 576px){
  .license-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .doc-info{
    &__group{
      gap: .5em;
      @include lastM(0 0 1.5em);
    }
    &__col{
      &:nth-child(2){
        grid-column: 1/2;
      }
    }
  }

  .doc-info2{
    gap: 1em;
  }

  .social-project{
    &__top{
      display: block;
      .share{
        margin: 1.5em 0 0;
      }
    }
  }

  .achievement{
    &__text{
      font-size: 1.25em;
    }
  }

  .text{
    img{
      border-radius: 1em;
      margin: 1em 0;
    }
  }
}
