.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba($black, .6);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: $mint;
        width: 39.25em;
        max-width: calc(100% - 2.5em);
        position: relative;
        z-index: 3;
        overflow: hidden;
        border-radius: .9375em;
    }
    &__scroll{
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100 * var(--vh));
        padding: 2.5em 3.125em 3.125em;
    }
    &__close {
        position: absolute;
        border-top: 0;
        top: .875em;
        right: .875em;
        background-color: transparent;
        width: 1.5em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                stroke: $primary;
            }
        }
    }
    .fg{
        textarea{
            height: 7.5em;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            max-width: 100%;
            border-radius: 0;
        }
        &__scroll{
            overflow: unset;
            overflow-x: hidden;
            padding: 3.5em 1em;
        }
        &__title{
            font-size: 1.75em;
        }
    }
}


