.blood{
  .prices-filter{
    justify-content: unset;
    .m-btn{
      margin-right: 1.5em;
    }
  }
  &__desc{
    display: flex;
    flex-wrap: wrap;
    margin-top: 3.125em;
    &-item{
      font-size: 1.0625em;
      line-height: 130%;
      margin-right: 2.5em;
      &:last-child{
        margin-right: 0;
      }
    }
  }
  .text{
    p,li{
      font-size: 1.125em;
      line-height: 150%;
    }
    h3{
      margin-bottom: .67em;
    }
    p{
      margin-bottom: .75em;
    }
    ol,ul{
      margin-bottom: 1.25em;
    }
    hr{
      height: 2em;
      display: block;
      opacity: 0;
      margin: 0;
    }
  }
}

.blood-info{
  position: sticky;
  top: 1em;
  border-radius: 1.875em;
  padding: 2.5em 3.125em;
  background-color: $mint;
  color: rgba($black,.7);
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 1.25em;
    border-bottom: 1px solid #C2C5D1;
  }
  &__doc{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: #50B18D;
    svg{
      width: 2.75em;
      margin-right: .625em;
    }
    span{
      font-size: 1.125em;
      line-height: 130%;
    }
  }
  &__props{
    @include grid100gap(.5em);
    margin-top: 1.25em;
  }
  &__prop{
    line-height: 140%;
  }
  &__sum{
    margin-top: 1.125em;
    @include grid100gap(.5em);
    &-title{
      line-height: 140%;
    }
    &-num{
      font-size: 2em;
      line-height: 140%;
      color: #50B18D;
      font-weight: 600;
    }
  }
  &__duration{
    margin-top: 1.25em;
    padding: 1.25em 5.5em 1.25em 0;
    border-top: 1px solid #C2C5D1;
    border-bottom: 1px solid #C2C5D1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-text{
      line-height: 140%;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.875em;
  }
}

.info{
  position: relative;
  &:hover{
    .info__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    cursor: pointer;
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    position: absolute;
    bottom: 99%;
    left: 50%;
    transform: translateX(-50%);
    padding-bottom: .5em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__inner{
    text-align: center;
    border-radius: .375em;
    padding: .75em .625em;
    width: 12.5em;
    height: 4.0625em;
    @include bgImgSize(100%);
    background-image: url(../img/bg.svg);
  }
  &__text{
    font-size: .75em;
    line-height: 140%;
  }
}


.blood-additional{
  margin-top: 5em;
  &__items{
    margin-top: 3.125em;
    @include grid100gap(2.125em);
  }
}


@media screen and (max-width: 992px){
  .blood{
    &__desc{
      margin-top: 1em;
      &-item{
        font-size: .875em;
        margin-top: .5em;
      }
    }
  }

  .blood-additional{
    margin-top: 3em;
    &__items{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .blood{
    .text{
      font-size: .75em;
    }
    .analysis-card__title{
      font-size: 1em;
    }
  }

  .blood-info{
    font-size: .75em;
    padding: 1.25em;
  }
}