._contrast{
  .header-branch{
    font-size: 1.125em;
  }
  .m-btn{
    svg,img{
      display: none;
    }
  }
  .m-btn-transparent{
    color: $black;
  }
  .m-btn-primary-outline,
  .m-btn-primary{
    background: #000;
    color: #fff;
    border-color: #000;
    &:hover{
      background: #000;
      color: #fff;
      border-color: #000;
    }
  }
  .service-banner,
  .main-slider{
    color: #fff;
    background: #000;
  }

  .have-questions__img,
  .service-banner__img{
    display: none;
  }

  .socials{
    font-size: 1.25em;
  }

  .footer,
  .location,
  .blog-card,
  .info-block,
  .consult,
  .service-search{
    .lime-text,
    .primary-text{
      color: #000;
    }
  }

  .achievement,
  .have-questions,
  .service-banner,
  .advantage-card{
    .lime-text,
    .primary-text{
      color: #fff;
    }
  }

  .advantage-card{
    padding-top: 2em;
    font-size: 1.125em;
    background: #000;
    color: #fff;
  }

  ._bullets .swiper-pagination-bullet{
    background: rgba(#000,.6);
  }
  ._bullets .swiper-pagination-bullet-active{
    background: #000;
  }

  .trust-card{
    background: #000;
    color: #fff;
    &__num{
      color: #fff;
    }
  }

  .doctor-card{
    &__prof{
      color: #000;
    }
  }

  .achievement,
  .have-questions__inner,
  .new-feedback{
    background: rgba(#000,.7);
  }

  .play-btn{
    svg{
      display: block;
    }
  }

  .blog-card__badge{
    background: #000;
  }

  .location-info{
    img{
      display: none;
    }
  }

  .media-card__link{
    color: #000;
  }

  .media-card__date img{
    display: none;
  }

  .pagination{
    &__items{
      border-color: #000;
    }
    &__item{
      color: #000;
      border-color: #000;
      &._active{
        background: #000;
        color: #fff;
      }
    }
    path{
      fill: #000;
    }
  }

  .social-card__item{
    img{
      display: none;
    }
  }

  .achievement__img{
    opacity: 0;
  }

  .reason-card{
    background: rgba(#000,.7);
    color: #fff;
    font-size: 1.25em;
    &__img{
      display: none;
    }
    .black70-text,
    .primary-text{
      color: #fff;
    }
  }

  .vacancy-card{
    .primary-text{
      color: #000;
    }
  }


  .modal__dialog,
  .author,
  .m-form__inner,
  .vacancy-form{
    background: rgba(#000,.7);
    color: #fff;
    &__title,
    .black70-text,
    .primary-text{
      color: #fff;
    }
  }

  .service-link{
    background: rgba(#000,.7);
    color: #fff;
    font-size: 1.25em;
    img{
      display: none;
    }
  }

  .service-main{
    background: rgba(#000,.7);
  }

  .price-item{
    font-size: 1.25em;
    .primary-text{
      color: #000;
    }
  }

  .work-card{
    .lime-text{
      color: #000;
    }
    &__service,
    &__time{
      img{
        display: none;
      }
    }
  }

  .badge{
    background: rgba(#000,.7);
    color: #fff;
  }

  .doctor-card2{
    .lime-text{
      color: #000;
    }
  }

  .advices-text{
    &:after,
    &:before{
      border-color: #000;
    }
  }

  .lab__tab{
    color: #000;
    &:not(._active){
      background: #000;
      border-color: #000;
      color: #fff;
    }
    &._active{
      border-color: #000;
    }
  }
  .lab__items{
    border-color: #000;
  }

  .analysis-card{
    .primary-text{
      color: #000;
    }
    &__item{
      img{
        display: none;
      }
    }
    &__toggle{
      path{
        fill:#000;
      }
    }
  }

  .specialization__title{
    img{
      display: none;
    }
  }

  .experience__btn._active{
    background: #000;
  }

  .edu__items:before,
  .edu-item:before{
    background: #000;
  }

  .sale-card__date{
    background: #000;
  }

  .qa-card{
    background: rgba(#000,.7);
    color: #fff;
    .primary-text{
      color: #fff;
    }
  }

  .contacts__item{
    font-size: 1.25em;
    img{
      display: none;
    }
  }

  .modal__close{
    width: 2.5em;
    path{
      fill: #fff;
    }
  }

  @media screen and (min-width: 993px){
    .conf{
      font-size: 1.25em;
    }
    .tax__groups{
      width: 100%;
      font-size: 1.375em;
    }
  }
}
