.adult-program{
  &__grid{
    margin-top: 3.125em;
  }
}

.program-card{
  border-radius: 1.25em;
  border: 1px solid $gray;
  padding: 1.56em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__desc{
    margin-top: 1em;
  }
  &__doctors{
    display: flex;
    margin-top: 1.5em;
  }
  &__doctor{
    width: 3.5em;
    height: 3.5em;
    &:not(:first-child){
      margin-left: -1.25em;
    }
    img{
      width: 100%;
      height: 100%;
      display: block;
      border-radius: 50%;
      border: .25em solid #fff;
    }
  }
  &__price{
    margin: .75em 0;
  }
  &__btn{
    margin-top: auto;
    width: 100%;
  }
}
