.reason-card{
  padding: 1.38em 2em 2.31em;
  border-radius: 0.9375em;
  background: rgba(215, 241, 232, 0.60);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    height: 4.25em;
    width: 100%;
    img{
      margin: 0 auto;
      display: block;
      height: 100%;
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.94em);
  }
}

.vacancy-card{
  height: 100%;
  padding: 3.16em 3.25em;
  text-align: center;
  border-radius: 1.25em;
  border: 1px solid #C2C5D1;
  display: flex;
  flex-direction: column;
  &__inner{
    @include grid100gap(2.81em);
  }
  &__source{
    @include center;
  }
  &__site{
    width: 2.8125em;
    display: block;
    margin-left: 1.0625em;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    padding-top: 2.81em;
    margin-top: auto;
  }
  &__link{
    width: 18.9375em;
    max-width: 100%;
    @media screen and (min-width: 993px){
      height: 4em;
      span{
        font-size: 1.125em;
      }
    }
  }
}

.vacancy{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 58em) 47.875em;
    gap: 1.5em;
    justify-content: space-between;
  }
  &__info{
    display: flex;
    align-items: center;
    &-item{
      display: flex;
      align-items: center;
      font-weight: 600;
      @include lastM(0 1.25em 0 0);
      img{
        width: 3.25em;
        flex-shrink: 0;
        margin-right: .625em;
      }
    }
  }
  &__group{
    margin-top: 2.25em;
    @include grid100gap(.81em);
    max-width: 50em;
  }
  .vacancy-card__source{
    margin-top: 2.25em;
    justify-content: unset;
  }
}

.vacancy-form{
  padding: 2.5em 3.125em 4.375em;
  border-radius: 1.25em;
  background-color: $mint;
  &__top{
    @include grid100gap(1.25em);
    max-width: 55em;
    margin: 0 auto;
    .vacancy-form__title{
      text-align: left;
    }
  }
  &__title{
    text-align: center;
    color: $primary;
  }
  &__fields{
    margin-top: 1.875em;
    @include grid100gap(1.25em);
  }
  &__btn,
  &__hint{
    width: 100%;
    margin-top: 1.875em;
  }
  .files{
    @include grid100gap(.5em);
    .file-input{
      order: -1;
    }
  }
  .file{
    margin-top: .5em;
  }
}


@media screen and (max-width: 992px){
  .vacancy-card{
    padding: 1em;
    &__inner{
      gap: 1em;
    }
    &__nav{
      padding-top: 1.25em;
    }
    &__site{
      width: 2.25em;
      margin-left: .75em;
    }
  }

  .vacancy{
    &__grid{
      grid-template-columns: 100%;
    }
    .vacancy-card__source,
    &__group{
      margin-top: 1.5em;
    }
    &__info{
      &-item{
        img{
          width: 2.5em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .vacancy{
    &__info{
      display: block;
      &-item{
        @include lastM(0 0 1em);
      }
    }
  }

  .vacancy-form{
    padding: 1em;
    &__top{
      gap: .5em;
    }
    &__fields{
      gap: .75em;
      margin-top: 1em;
    }
    &__hint{
      margin-top: 1em;
    }
  }
}
