.footer{
  padding: 6.25em 0;
  background-color: $lightMint;
  &__inner{
    @include grid100gap(4.375em);
  }
  &__logo{
    display: block;
    width: 17.0625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top,
  &__center,
  &__bottom{
    display: grid;
    grid-template-columns: 27em calc(100% - 29em);
    gap: 2em;
  }
  &__links{
    margin-top: 1.875em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-weight: 700;
    font-size: 1em;
    line-height: 140%;
    @include lastM(0 0 1.56em);
  }
  .socials{
    &__item{
      width: 2.625em;
      @include lastM(0 1.25em 0 0);
    }
  }
  &__hint{
    grid-column: 2/3;
    font-size: 1.5em;
    line-height: 110%;
    letter-spacing: 0.195em;
    text-transform: uppercase;
    opacity: .5;
    color: $primary;
  }
  &__by{
    width: 8.94em;
    display: block;
    &:hover{
      path{
        fill: $black;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__link2{
    font-size: .875em;
    text-decoration: underline;
  }
}

.footer-contacts{
  margin-top: 2.19em;
  @include grid100gap(.94em);
}

.scroll-up {
  position: fixed;
  width: 5.625em;
  height: 5.625em;
  border-radius: 50%;
  bottom: 50px;
  right: 35px;
  z-index: 8;
  pointer-events: none;
  opacity: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.69em 0;
  color: #fff;
  font-weight: 500;
  line-height: 230%;
  &--active {
    opacity: 1;
    pointer-events: unset;
  }
  svg{
    width: 1em;
    height: auto;
  }
  span{
    font-size: 1em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0;
    &__inner{
      gap: 2em;
    }
    &__top,
    &__center,
    &__bottom{
      grid-template-columns: 100%;
    }
    &__hint{
      grid-column: 1/-1;
      font-size: 1.25em;
    }
    &__links{
      margin-top: 1em;
    }
    &__link{
      @include lastM(0 0 .5em);
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__by,
    &__logo{
      width: 8em;
    }
    &__hint{
      font-size: 1em;
    }
  }
}
