.advantage-card{
  padding: 1.125em 1.56em 1.56em;
  background-color: $lightMint;
  border-radius: .625em;
  position: relative;
  overflow: hidden;
  &__num{
    position: absolute;
    font-size: 4.75em;
    font-weight: 700;
    color: #fff;
    right: 0;
    top: -.25em;
  }
  &__info{
    @include grid100gap(.3125em);
    position: relative;
    z-index: 2;
  }
  &__desc{
    opacity: .7;
  }
}

.trust-card{
  border-radius: 0.9375em;
  background-color: $lightMint;
  background-image: url(../img/trust-bg.svg);
  background-size: cover;
  background-position: right top;
  padding: 0 2em 1.94em;
  &__num{
    font-size: 6em;
    line-height: 130%;
    font-weight: 700;
    color: $lime;
  }
  &__desc{
    margin-top: .375em;
  }
}

.doctor-card{
  border-radius: 1.25em;
  border: 1px solid $gray;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    @include imgCover(67%);
    img{
      border-radius: 1.25em 1.25em 0 0;
    }
  }
  &__content{
    padding: 1.56em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: .625em;
  }
  &__prof{
    letter-spacing: 0.04375em;
    text-transform: uppercase;
    font-weight: 700;
    color: $lime;
  }
  &__desc{
    margin-top: .94em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.875em;
    display: flex;
    justify-content: space-between;
    .m-btn-transparent{
      padding: 0 .625em;
    }
  }
}

.review-card{
  &__img{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__rating{
    display: flex;
    align-items: center;
    margin-top: .94em;
    .rating-input{
      margin-left: .94em;
    }
  }
}

.swiper-slide._big{
  .introduction-card{
    height: 100%;
    &__img{
      height: 100%;
      padding-bottom: 77%;
    }
  }
}

.introduction-card{
  height: 100%;
  &__inner{
    height: 100%;
    &._double{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .introduction-card__img{
        padding-bottom: 76.6%;
      }
    }
  }
  &__img{
    @include imgCover(161%);
    display: block;
    img{
      border-radius: 0.9375em;
    }
  }
}

.feedback-card{
  padding: 1.25em 1.875em 1.875em;
  border-radius: 0.9375em;
  background-color: #fff;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__source,
  &__info{
    @include grid100gap(.3125em);
  }
  &__source{
    text-align: right;
    flex-shrink: 0;
  }
  .rating-input{
    &__item{
      width: 1.25em;
      @include lastM(0 .125em 0 0);
    }
  }
  &__text{
    margin-top: 1.25em;
    @include truncate(13);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2.19em;
  }
  &__doctor{
    display: flex;
    align-items: center;
    .badge{
      margin-left: .94em;
    }
  }
  &__answer{
    margin-top: 3.125em;
    display: flex;
    align-items: center;
    img{
      width: 2.1em;
      flex-shrink: 0;
      margin-right: .94em;
    }
    &-text{
      @include truncate(2);
    }
  }
}

.blog-card{
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    @include imgCover(58.4%);
    img{
      border-radius: .9375em;
    }
  }
  &__badge{
    position: absolute;
    top: .66em;
    left: .72em;
    padding: 0 1em;
    border-radius: 0.5em;
    background-color: $primary;
    height: 1.9375em;
    display: flex;
    align-items: center;
    color: #fff;
  }
  &__content{
    margin-top: 1.875em;
    @include grid100gap(.94em);
  }
  &__desc{
    @include truncate(4);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.56em;
    @include grid100gap(1.56em);
  }
  &__doctor{
    display: flex;
    align-items: flex-start;
    .avatar{
      margin-right: .94em;
    }
    &-info{
      @include grid100gap(.3125em);
    }
  }
  &__nav{
    display: flex;
  }
  &__link{
    padding: 0 .625em;
  }
}

.video-card{
  .video{
    margin-bottom: 1.25em;
  }
}

.social-card,
.media-card{
  border-radius: 1.25em;
  border: 1px solid #C2C5D1;
  display: flex;
  flex-direction: column;
  &__content{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
}

.media-card{
  &__logo{
    padding: 1.5em;
    height: 5.625em;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__img{
    display: block;
    width: calc(100% + 2px);
    margin-left: -1px;
    @include imgCover(48.5%);
  }
  &__content{
    padding: 1.25em 1.5em;
  }
  &__info{
    @include grid100gap(1em);
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;
    padding-top: 1.16em;
  }
  &__link{
    color: #069E5C;
  }
  &__date{
    display: flex;
    align-items: center;
    img{
      margin-right: .625em;
      flex-shrink: 0;
      width: 2.1875em;
    }
  }
}

.social-card{
  &__img{
    width: calc(100% + 2px);
    margin: -1px 0 0 -1px;
    display: block;
    @include imgCover(79%);
    img{
      border-radius: 1.25em 1.25em 0 0;
    }
  }
  &__content{
    padding: .81em 1.94em 1.56em;
  }
  &__info{
    @include grid100gap(.94em);
  }
  &__nav{
    padding-top: .94em;
    margin-top: auto;
    display: flex;
    justify-content: center;
  }
  &__btn{
    max-width: 100%;
    width: 14.6875em;
    @media screen and (min-width: 993px){
      height: 4em;
      border-radius: 1em;
      span{
        font-size: 1.125em;
      }
    }
  }
  &__items{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.56em;
  }
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 1.75em;
      flex-shrink: 0;
      margin-right: .625em;
    }
  }
}


@media screen and (max-width: 992px){
  .advantage-card{
    padding: 1em;
    &__num{
      font-size: 3.25em;
    }
  }

  .trust-card{
    padding: 0 1em 1em;
    &__num{
      font-size: 3.25em;
    }
  }

  .review-card{
    .rating-input{
      margin-left: .5em;
    }
  }

  .introduction-card{
    &__img{
      img{
        border-radius: 0.5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .doctor-card{
    &__content{
      padding: .5em;
    }
    &__top{
      display: block;
      margin-bottom: .5em;
    }
    &__experience{
      margin-top: .5em;
    }
    &__nav{
      padding-top: 1em;
      @include grid100gap(.5em);
    }
  }

  .feedback-card{
    padding: 1em;
    &__bottom{
      padding-top: 1em;
    }
    &__doctor{
      flex-direction: column;
      align-items: flex-start;
      .badge{
        margin: .5em 0 0;
      }
    }
    &__answer{
      margin-top: 1em;
    }
  }

  .blog-card{
    &__content{
      margin-top: 1em;
      gap: .5em;
    }
    &__bottom{
      padding-top: 1em;
      gap: 1em;
    }
  }

  .media-card{
    border-radius: 1em;
    &__logo{
      padding: 1em;
      height: 4em;
    }
    &__content{
      padding: 1em;
    }
    &__info{
      gap: .5em;
    }
    &__nav{
      flex-direction: column;
      align-items: flex-start;
    }
    &__date{
      margin-top: .5em;
      img{
        width: 1.75em;
      }
    }
  }

  .social-card{
    &__content{
      padding: 1em;
    }
    &__item{
      img{
        width: 1.25em;
        margin-right: .375em;
      }
    }
  }
}
