.sale-card{
  border-radius: 2.5em;
  border: 2px solid #C2C5D1;
  display: flex;
  flex-direction: column;
  &__top{
    width: calc(100% + 4px);
    margin: -2px 0 0 -2px;
    position: relative;
  }
  &__img{
    display: block;
    @include imgCover(50%);
    img{
      border-radius: 2.5em;
    }
  }
  &__content{
    padding: 5.75em 1.875em 1.875em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__info{
    @include grid100gap(2em);
  }
  .text32{
    line-height: 130%;
  }
  &__nav{
    padding-top: 2.875em;
    margin-top: auto;
    display: flex;
  }
  &__btn{
    width: 14.6875em;
  }
  &__date{
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 8.125em;
    height: 8.125em;
    border-radius: 50%;
    background-color: $primary;
    color: #fff;
    @include center;
    flex-direction: column;
  }
  .blog-card__badge{
    left: 1.875em;
    top: 1.875em;
    min-width: 9em;
    height: 2.1875em;
    justify-content: center;
    border-radius: 1.25em;
  }
  @media screen and (min-width: 993px){
    &._row{
      display: grid;
      grid-template-columns: 50% 50%;
      grid-column: 1/-1;
      .sale-card__top{
        order: 2;
        margin-left: 0;
        margin-right: -2px;
      }
      .sale-card__content{
        padding: 1.875em;
        max-width: 52em;
      }
      .blog-card__badge{
        left: unset;
        right: 1.875em;
      }
      .sale-card__date{
        top: 50%;
        left: 0;
      }
    }
  }
}

.sales-collection{
  .swiper-slide{
    width: 50%;
  }
}

.live-item{
  padding-bottom: 4.375em;
  &.live-item2{
    padding-bottom: 8.75em;
  }
  &__content{
    grid-column: 1/3;
  }
}

.live-article{
  padding: 2.78em 1.875em 2.16em;
  border-radius: 1.25em;
  border: 2px solid #C2C5D1;
  .social-project__top{
    &._mb0{
      margin-bottom: 0;
      margin-top: 2.625em;
    }
    .social-card__items{
      font-weight: 600;
    }
  }
}

.live-group{
  @include grid100gap(1.5em);
  @include lastM(0 0 3em);
}

.author{
  padding: 3.125em;
  border-radius: 1.875em;
  background-color: $mint;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 2.25em;
  text-align: center;
  .avatar{
    width: 14.6875em;
    height: 14.6875em;
    margin-bottom: 1em;
  }
  &__info{
    margin-top: 1.5em;
    @include grid100gap(.5em);
    max-width: 28em;
  }
}

.sale-item{
  padding-bottom: 4.375em;
}


@media screen and (max-width: 992px){
  .sales-collection{
    .swiper-slide{
      width: 100%;
    }
  }

  .live-item{
    padding-bottom: 1em;
    &.live-item2{
      padding-bottom: 2em;
    }
    .grid-column3{
      grid-template-columns: 100%;
    }
    &__content{
      grid-column: 1/2;
    }
  }

  .live-group{
    gap: .5em;
    @include lastM(0 0 1.5em);
  }

  .sale-item{
    padding-bottom: 1em;
  }
}


@media screen and (max-width: 576px){
  .sale-card{
    border-radius: 1em;
    &__img{
      padding-bottom: 70%;
      img{
        border-radius: 1em;
      }
    }
    .blog-card__badge{
      left: 1em;
      top: 1em;
      min-width: 7em;
      height: 1.75em;
    }
    &__date{
      font-size: .625em;
    }
    &__content{
      padding: 2.5em 1em 1em;
    }
    &__info{
      gap: .5em;
    }
    &__nav{
      padding-top: 1em;
    }
  }

  .live-article{
    padding: 1em .5em;
    border-radius: 1em;
    border-width: 1px;
  }

  .author{
    padding: 1em;
    border-radius: 1em;
    margin-bottom: 1em;
    .avatar{
      width: 8em;
      height: 8em;
    }
    &__info{
      margin-top: .5em;
      max-width: 16em;
    }
    .text36{
      font-size: 1.25em;
    }
  }
}
