@import "../vars.scss";
.breadcrumbs {
    padding: 1.25em 0;
    margin-bottom: .625em;
    &__item {
        font-weight: 300;
        display: inline;
        &:not(:first-child) {
            &:before {
                content: ' / ';
                color: rgba(6, 158, 92, 0.60);
                margin: 0 .25em;
                font-weight: 700;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: .75em 1em;
        margin-left: -1em;
        width: calc(100% + 2em);
        margin-bottom: 0;
        display: flex;
        overflow-x: auto;
        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }
        &__item{
            flex-shrink: 0;
        }
    }
}
