.text48{
  font-size: 3em;
  line-height: 130%;
}

.text36{
  font-size: 2.25em;
  line-height: 130%;
}

.text h3,
.text32{
  font-size: 2em;
  line-height: 110%;
}

.text28{
  font-size: 1.75em;
  line-height: 130%;
}

.conf-text h3,
.text24{
  font-size: 1.5em;
  line-height: 130%;
}

.text22{
  font-size: 1.375em;
  line-height: 140%;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text18{
  font-size: 1.125em;
  line-height: 130%;
}

.text{
  word-break: break-word;
}

.text h5,
.m-list li,
.text p,
.text li,
.breadcrumbs__item,
.m-link,
.text16,
.menu-dropdown__link{
  font-size: 1em;
  line-height: 140%;
}

.caption-text,
.text14{
  font-size: .875em;
  line-height: 140%;
}

.text10{
  font-size: .625em;
  line-height: 140%;
}

.caption-text{
  letter-spacing: 0.04375em;
  font-weight: 700;
  text-transform: uppercase;
}

.lh150{
  line-height: 150%;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.dark-gray-text{
  color: $darkGray;
}

.gray-text{
  color: $gray;
}

.primary-text{
  color: $primary;
}

.black70-text{
  color: rgba($black,.7);
}

.lime-text{
  color: $lime;
}

.text ul,
.m-list{
  li{
    line-height: 150%;
    &:before{
      content: ' ';
    }
  }
}

.underline-text{
  text-decoration: underline;
}


@media screen and (max-width: 992px){
  .text48{
    font-size: 2em;
  }

  .text h3,
  .text36,
  .text32{
    font-size: 1.875em;
  }

  .conf-text h3,
  .text28,
  .text24{
    font-size: 1.25em;
  }

  .text22,
  .text20{
    font-size: 1.125em;
  }

  .text18{
    font-size: 1em;
  }
}


@media screen and (max-width: 576px){
  .text48{
    font-size: 1.5em;
  }

  .text h3,
  .text36,
  .text32{
    font-size: 1.375em;
  }

  .conf-text h3,
  .text28,
  .text24{
    font-size: 1.125em;
  }

  .text22,
  .text20{
    font-size: 1em;
  }

  .text li,
  .text h5,
  .m-list li,
  .text p,
  .text18,
  .breadcrumbs__item,
  .text16{
    font-size: .875em;
  }
}
